const prices = {
  '4400yen1limited': { money: 4400, paid: 2200, free: 4400 },
  '11800yen': { money: 11800, paid: 5900, free: 1200 },
  '6000yen': { money: 6000, paid: 3000, free: 550 },
  '3000yen': { money: 3000, paid: 1500, free: 250 },
  '1500yen': { money: 1500, paid: 750, free: 100 },
  '1000yen': { money: 1000, paid: 500, free: 50 },
  '480yen': { money: 480, paid: 240, free: 10 },
  '160yen': { money: 160, paid: 80, free: 0 },
}

const spins = {
  'paid1500': { n: 10, medal: 6 },
  'free3000': { n: 10, medal: 11 },
  'free300': { n: 1, medal: 1 },
  'ticket1500': { n: 10, medal: 0 },
}

const rato = {
  character: {
    'star3Pickup': 1,
    'star3': 3,
    'star2': 14,
    'star1': 36.5,
  },
  memoria: {
    'SSRPickup': 0.5,
    'SSR': 1.5,
    'SR': 7,
    'R': 36.5
  }
}
const sortedRarities = [];
for (const [category, rarities] of Object.entries(rato)) {
  for (const [rarity, weight] of Object.entries(rarities)) {
    sortedRarities.push({ category, rarity, weight });
  }
}
// 
sortedRarities.sort((a, b) => b.weight - a.weight);




const styleLabelTitleInline = {
  boxSizing: 'border-box',
  display: 'flex',        // div
  justifyContent: 'center', // 
  alignItems: 'center',   // 
  width: 40,
  height: 40, // Img
  backgroundColor: darken(mainBackgroundColor, 0.2),
  fontSize: '14px',
  fontWeight: 'bold',
}

const GachaItem = ({ name }) => {

  const category = name.includes('') ? 'character' : 'memoria'

  return (
    <Grid>
      <div
        style={{
          position: 'relative',
          display: 'flex',
          alignItems: 'center',
          // width: '60px', // 
          margin: '2px'
        }}

      >
        <Img name={name + (name.includes('') ? 'g' : '')} width={100} height={160} />
        <div style={{
          position: 'absolute',
          bottom: 0,
          left: '50%',
          transform: 'translateX(-50%)',
          fontWeight: 'bold',
          backgroundColor: 'rgba(0, 0, 0, 0.8)',
          textAlign: 'center',
          width: '100%',
          lineHeight: 1,
        }}>
          <div style={{
            display: 'block',
            color: mainColor,
            fontSize: '16px',
            whiteSpace: 'nowrap',
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            // maxWidth: '60px',
          }}>
            {category === 'character'
              ? <>{[...Array(character[name].star)].map((_, index) => (
                <StarIcon key={index}
                  style={{
                    color: 'yellow',
                    fontSize: '18px',
                    marginLeft: -2,
                    marginRight: -2,
                    marginTop: 0,
                    marginBottom: -4,
                  }}
                />
              ))}<br />
                {name.split('')[0]}</>
              : <>{memoria[name].rarity}<br />{name}</>
            }
          </div>
        </div>
      </div>
    </Grid>
  )
}




const GachaPage = () => {
  const [totalMoney, setTotalMoney] = useState(0);
  const [totalPaid, setTotalPaid] = useState(0);
  const [totalFree, setTotalFree] = useState(0);
  const [limitedClicked, setLimitedClicked] = useState(false); // 1
  const [gachaResults, setGachaResults] = useState([]); // 
  const [pickupCharacters, setPickupCharacters] = useState(['Romance Hearts','']);
  const [pickupMemorias, setPickupMemorias] = useState(['']);
  const [totalSpins, setTotalSpins] = useState(0); // 
  const [totalMedals, setTotalMedals] = useState(0); // 
  const [ticket1500Count, setTicket1500Count] = useState(0); // ticket1500
  const [snackbarOpen, setSnackbarOpen] = useState(false);



  // 
  const [roundCount, setRoundCount] = useState(0);
  const [spinCountInRound, setSpinCountInRound] = useState(0);


  // 
  const handlePickupCharacterChange = (newPickupCharacters) => {
    setPickupCharacters(newPickupCharacters);
  };
  const handlePickupMemoriaChange = (newPickupMemorias) => {
    setPickupMemorias(newPickupMemorias);
  };

  const sortedGachaContents = {
    character: {
      star3: [],
      star3Pickup: pickupCharacters.map(name => {
        return ({
          ...character[name], name
        })
      }),
      star2: [],
      star1: [],
    },
    memoria: {
      SSR: [],
      SSRPickup: pickupMemorias.map(name => {
        return ({
          ...memoria[name], name
        })
      }),
      SR: [],
      R: [],
    }
  };
  Object.entries(characters).forEach(([name, el]) => {

    if (el.get === '') {
      if (pickupCharacters.includes(el.name)) {
        sortedGachaContents.character[`star${el.star}Pickup`].push({ ...el, name });
      } else {
        sortedGachaContents.character[`star${el.star}`].push({ ...el, name });
      }

    }
  });
  Object.entries(memorias).forEach(([name, el]) => {
    if (el.get === '') {
      if (pickupMemorias.includes(el.name)) {
        sortedGachaContents.memoria[`${el.rarity}Pickup`].push({ ...el, name });
      } else {
        sortedGachaContents.memoria[el.rarity].push({ ...el, name });
      }
    }
  });


  const initAccumulatedResults = () => {
    const initialResults = {};

    // 
    pickupCharacters.forEach(name => {
      if (!initialResults[name]) {
        initialResults[name] = 0;
      }
    });




    // Object.entries(characters).forEach(([name, el]) => {
    //   if (el.get === '') {
    //     initialResults[name] = 0;
    //   }
    // });
    sortedGachaCharNames.map(name => {
      initialResults[name] = 0;

    })

    pickupMemorias.forEach(name => {
      if (!initialResults[name]) {
        initialResults[name] = 0;
      }
    });


    Object.entries(memorias).forEach(([name, el]) => {
      if (el.get === '') {
        initialResults[name] = 0;
      }
    });

    return initialResults;
  };
  const [accumulatedResults, setAccumulatedResults] = useState(initAccumulatedResults());



  //
  const handlePriceClick = (priceKey) => {
    if (priceKey === '4400yen1limited') {
      setLimitedClicked(true); // 1
    }
    setTotalMoney(totalMoney + prices[priceKey].money);
    setTotalPaid(totalPaid + prices[priceKey].paid);
    setTotalFree(totalFree + prices[priceKey].free);
  };




  const selectRandomItem = () => {

    // 
    let random = Math.random() * 100;//totalWeight 100
    let selectedCategory;
    let selectedRarity;


    for (const { category, rarity, weight } of sortedRarities) {
      random -= weight;
      if (random < 0) {
        selectedCategory = category;
        selectedRarity = rarity;
        break;
      }
    }

    // 
    if (selectedRarity.endsWith('Pickup') && sortedGachaContents[selectedCategory][selectedRarity].length === 0) {
      selectedRarity = selectedRarity.replace('Pickup', ''); // 'star3Pickup' -> 'star3' 
    }

    const items = sortedGachaContents[selectedCategory][selectedRarity];
    return items[Math.floor(Math.random() * items.length)];
  };



  // 
  const handleSpinClick = (spinKey) => {

    const isPaidSpin = spinKey.includes('paid');
    const isPaid1500 = spinKey === 'paid1500';
    const isFreeSpin = spinKey === 'paid1500' && spinCountInRound === 2;
    const isTicket1500 = spinKey === 'ticket1500';


    // paid1500
    if (isPaid1500 && (roundCount >= 5 || spinCountInRound >= 3)) {
      console.log('');
      return;
    }

    // ticket1500
    if (isTicket1500 && ticket1500Count >= 2) {
      console.log('ticket1500');
      return;
    }


    let spinCost;
    if (isFreeSpin) {
      spinCost = 0;
    } else if (isTicket1500) {
      spinCost = 1500;
    } else {
      spinCost = Number(spinKey.split(/paid|free/)[1]);
    }

    const currentTotal = isPaidSpin ? totalPaid : totalFree;


    // 
    if (currentTotal >= spinCost) {
      const newTotal = currentTotal - spinCost;
      isPaidSpin ? setTotalPaid(newTotal) : setTotalFree(newTotal);

      const newResults = Array.from({ length: spins[spinKey].n }, selectRandomItem)
        .map(item => item.name);
      setGachaResults(newResults);

      const newAccumulatedResults = { ...accumulatedResults };
      newResults.forEach(itemName => {
        newAccumulatedResults[itemName] = (newAccumulatedResults[itemName] || 0) + 1;
        // console.log(itemName)
      });

      setAccumulatedResults(newAccumulatedResults);

      // 
      setTotalSpins(totalSpins + spins[spinKey].n);

      // ticket1500
      if (isTicket1500) {
        setTicket1500Count(ticket1500Count + 1);
      }

      // 
      if (isPaid1500) {
        if (spinCountInRound === 2) {
          setRoundCount(roundCount + 1); // 
          setSpinCountInRound(0);
        } else {
          setSpinCountInRound(spinCountInRound + 1);
        }
      }

      // 
      const medalsForSpin = isPaid1500 && isFreeSpin ? 0 : spins[spinKey].medal;
      setTotalMedals(totalMedals + medalsForSpin);

    } else {
      console.log('')
    }
  };



  return (
    <Grid container sx={{ margin: '4px' }}>

      <Grid xs={12}>
        <Grid container spacing={0.5}>
          {Object.keys(prices).map(priceKey => (
            <Grid key={priceKey}>
              <Button
                onClick={() => handlePriceClick(priceKey)}
                disabled={priceKey.includes('1limited') && limitedClicked} // 1
                sx={{ padding: 0 }}
              >
                <Img name={priceKey} width={96} isDark={priceKey.includes('1limited') && limitedClicked} />
              </Button>
            </Grid>
          ))}
        </Grid>
      </Grid>

      <Grid xs={12} sx={{marginTop:'4px'}}>
        <Grid container>
          {Object.keys(spins).map(spinKey => {

            const isPaidSpin = spinKey.includes('paid');

            const isPaid1500 = spinKey === 'paid1500';
            const isTicket1500 = spinKey === 'ticket1500';
            const isLimited = isPaid1500 && (roundCount >= 5 || spinCountInRound >= 3);
            const isTicketLimited = isTicket1500 && ticket1500Count >= 2
            const isFreeSpin = isPaid1500 && spinCountInRound === 2;

            let spinCost;
            if (isFreeSpin) {
              spinCost = 0;
            } else if (isTicket1500) {
              spinCost = 1500;
            } else {
              spinCost = Number(spinKey.split(/paid|free/)[1]);
            }


            const currentTotal = isPaidSpin ? totalPaid : totalFree;


            return (
              <Grid key={spinKey}>
                <IconButton
                  onClick={() => handleSpinClick(spinKey)}
                  sx={{ padding: 0, position: 'relative', fontWeight: 'bold' }} // 
                  disabled={(currentTotal < spinCost) || (isPaid1500 && isLimited) || (isTicket1500 && isTicketLimited)}
                >
                  <Img
                    name={spinKey}
                    width={100}
                    rhombus={true}
                    isDark={(currentTotal < spinCost) || (isPaid1500 && isLimited) || (isTicket1500 && isTicketLimited)}
                  />
                  {isPaid1500 && (
                    <span style={{
                      position: 'absolute',
                      top: 0,
                      color: mainColor,
                      backgroundColor: 'rgba(0, 0, 0, 0.5)', // 50%
                      borderRadius: '10px',
                      fontSize: '14px',
                      paddingLeft: '10px',
                      paddingRight: '10px',
                      lineHeight: (isPaid1500 && isLimited) ? 2 : 1.2,
                    }}>
                      {isPaid1500 && isLimited ? (
                        <span>5</span>
                      ) : (
                        <>
                          <span>{(roundCount + 1) + ''}</span>
                          <br />
                          <span>{'STEP ' + (spinCountInRound + 1)}</span>
                        </>
                      )}
                    </span>
                  )}

                  {isFreeSpin && (
                    <span style={{
                      position: 'absolute',
                      color: 'pink',
                      bottom: 22,
                      backgroundColor: 'rgba(0, 0, 0, 0.7)', // 50%
                      borderRadius: '20px',
                      fontSize: '14px',
                      padding: '2px',
                      paddingLeft: '20px',
                      paddingRight: '20px',
                    }}>
                      
                    </span>
                  )}

                  {(isTicket1500 && isTicketLimited) && (
                    <span style={{
                      position: 'absolute',
                      top: 0,
                      left: 0,
                      color: mainColor,
                      backgroundColor: 'rgba(0, 0, 0, 0.5)', // 50%
                      borderRadius: '10px',
                      fontSize: '14px',
                      paddingLeft: '10px',
                      paddingRight: '10px',
                      lineHeight: 2,
                    }}>
                      {'2'}
                    </span>
                  )}
                  {(isTicket1500 && !isTicketLimited) && (
                    <>
                      <span style={{
                        position: 'absolute',
                        color: mainColor,
                        bottom: 10,
                        backgroundColor: 'rgba(0, 0, 0, 0.7)', // 50%
                        borderRadius: '20px',
                        fontSize: '14px',
                        padding: 0,
                        paddingLeft: '10px',
                        paddingRight: '10px',
                        display: 'flex',        // 
                        alignItems: 'center',   // 
                      }}>
                        <Img name={'free'}
                          square={20}
                          isDark={(currentTotal < spinCost) || (isTicket1500 && isTicketLimited)}
                        />
                        <span style={{
                          color: ((currentTotal < spinCost) || (isTicket1500 && isTicketLimited)) ? 'gray' : mainColor
                        }}>1500</span>
                      </span>
                    </>
                  )}


                </IconButton>
              </Grid>
            );
          })}
        </Grid>
      </Grid>


      <Grid xs={12} sx={{ width: 200, fontWeight: 'bold', fontSize: '20px' }}>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <span style={styleLabelTitleInline}></span>
          <span style={{ marginLeft: '4px' }}>{totalMoney.toLocaleString()}</span>
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <Img name={'paid'} square={40} />
          <div style={{ marginLeft: '4px' }}>{totalPaid.toLocaleString()}</div>
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <Img name={'free'} square={40} />
          <div style={{ marginLeft: '4px' }}>{totalFree.toLocaleString()}</div>
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <Img name={'medal'} square={40} />
          <span style={{ marginLeft: '4px' }}>{totalMedals.toLocaleString()}</span>
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <span style={styleLabelTitleInline}></span>
          <span style={{ marginLeft: '4px' }}>{totalSpins.toLocaleString()}</span>
        </div>
      </Grid>




      <Grid xs={12} sx={{
        backgroundColor: darken(mainBackgroundColor, 0.2),
        marginTop: '8px',
        padding: '2px',

      }}>
        <div style={{ fontSize: '16px', fontWeight: 'bold', margin: '2px' }}></div>
        <Grid container>
          {gachaResults.length > 0 ? (
            gachaResults.map((name, index) => (
              <GachaItem key={index} name={name} />
            ))
          ) : (
            [...Array(10)].map((_, index) => (
              <Grid key={index}>
                {/*  */}
                <div style={{ width: 100, height: 160 }}></div>
              </Grid>
            ))
          )}
        </Grid>
      </Grid>


      <Grid xs={12} sx={{
        backgroundColor: darken(mainBackgroundColor, 0.2),
        marginTop: '8px',
      }}>
        <div style={{ fontSize: '16px', fontWeight: 'bold', margin: '4px' }}> / </div>
        <List>
          {Object.entries(accumulatedResults).map(([name, count], index, arr) => {
            const category = name.includes('') ? 'character' : 'memoria';
            let rarity, totalItems, probability;

            if (category === 'character') {
              // console.log(name)

              if (pickupCharacters.includes(name)) {

                rarity = 'star3Pickup';
              } else {

                rarity = `star${character[name].star}`;
              }
              probability = rato.character[rarity];
              totalItems = sortedGachaContents.character[rarity].length;
            } else {
              // console.log(name)
              if (pickupMemorias.includes(name)) {
                rarity = 'SSRPickup';
              } else {
                rarity = memoria[name].rarity.toUpperCase(); // 'SSR', 'SR', 'R'
              }
              probability = rato.memoria[rarity];
              totalItems = sortedGachaContents.memoria[rarity].length;
            }

            probability = (1 / totalItems) * probability; // 1

            const currentProbability = count / totalSpins


            const currentProbabilityValue = Math.round(Number(currentProbability.toFixed(6)) * 10000000) / 100000;
            const probabilityValue = Math.floor(Number(probability.toFixed(4)) * 1000) / 1000;
            let textColor;

            if (isNaN(currentProbabilityValue) || currentProbabilityValue === 0) {
              textColor = darken(mainColor, 0.5);
            } else if (currentProbabilityValue >= probabilityValue) {
              textColor = '#ffbf7f';
            } else {
              textColor = '#7fbfff';
            }



            return (
              <ListItem
                key={name}
                sx={{
                  display: 'flex',
                  fontSize: '16px',
                  borderTop: `1px solid ${lighten(mainBackgroundColor, 0.1)}`,
                  borderBottom: (index === arr.length - 1) ? `1px solid ${lighten(mainBackgroundColor, 0.1)}` : 'none',
                  '&:hover': {
                    backgroundColor: lighten(mainBackgroundColor, 0.01) // 
                  },
                  padding: 0,
                  paddingLeft: '4px',
                  paddingRight: '4px',
                }}
              >
                <div style={{ width: 40 }}>{category === 'character'
                  ? <div style={{}}>
                    {[...Array(character[name].star)].map((_, index) => (
                      <StarIcon key={index}
                        style={{
                          color: 'yellow',
                          fontSize: '16px',
                          marginLeft: 0,
                          marginRight: -5,
                        }}
                      />
                    ))}</div>
                  : memoria[name].rarity}</div>
                <div style={{
                  width: 190,
                  textOverflow: 'ellipsis', // 
                  overflow: 'hidden', // 
                  whiteSpace: 'nowrap', // 
                }}>
                  {name}
                </div>
                <div style={{ display: 'flex', justifyContent: 'flex-end', width: 20 }}>
                  <div style={{ fontWeight: 'bold', color: textColor }}>{count}</div>
                </div>

                <div style={{
                  width: 162,
                  textAlign: 'right',
                  fontFamily: 'monospace',
                  fontSize: '16px',
                }}>
                  <span style={{ color: textColor }}>{currentProbabilityValue.toFixed(3)}%</span>
                  <span> / {(probabilityValue.toFixed(3))}%</span>

                </div>
              </ListItem>
            )
          })}
        </List>
      </Grid>


    </Grid>
  );
}

export default GachaPage